<template>
    <v-app dark>

        <!-- Start navigation drawer -->
        <v-navigation-drawer app clipped>
            <v-list dense>
                <v-list-item v-for='(mode, index) in modes' :key='index'>
                    <v-switch dark inset color='purple' v-model='selectedModes' :label='mode.toUpperCase()' :value='mode'></v-switch>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
        <!-- End navigation drawer -->

        <!-- Start app bar -->
        <v-app-bar id='app-bar' app clipped-left>
            <!-- <v-toolbar-title>Japanese Conjugator</v-toolbar-title>-->
        </v-app-bar>
        <!-- End app bar -->

        <!-- Start main content -->
        <v-content>
            <VerbQuiz :verb='currentVerb' @nextVerb='nextVerb'></VerbQuiz>
        </v-content>
        <!-- End main content -->

        <!-- Start footer -->
        <v-footer app>
            <span>&copy; 2020</span>
        </v-footer>
        <!-- End footer  -->

    </v-app>
</template>

<script>
    import VerbQuiz from '@/components/VerbQuiz';
    import verbs from './libs/jpVerbs';

    export default {
        name: 'App',

        components: {
            VerbQuiz,
        },

        data: () => ({
            currentVerb: undefined,
            currentIndexVerb: 0,
            modes: ['present polite', 'present polite negative', 'present negative',
                    'past', 'past polite', 'past negative', 'past polite negative',
                    'desire', 'potential', 'te form', 'passive', 'causative'],
            selectedModes: ['present polite'],
        }),

        methods: {
            nextVerb() {
                if (this.currentIndexVerb < verbs.length) {
                    this.currentVerb = verbs[this.currentIndexVerb];
                    this.currentIndexVerb++;
                }
            }
        },

        created() {
            this.nextVerb();
            this.$vuetify.theme.dark = true;
        }
    };
</script>

<style type='text/css' scoped>
    #app-bar {
        background-image: url('./assets/app-bar-texture.jpg');
        background-repeat: repeat-x;
    }
</style>
